import React from "react";
import axios from "axios";
import BpChart from "../BpChart/BpChart";
import WeightChart from "../WeightChart/WeightChart";

class UserDetails extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      user: []
    };
    this.url = "http://54.251.159.245";
    this.userId = props.match.params.userId;
  }

  componentDidMount() {
    axios
      .get(`${this.url}/api/get-user`, { params: { userId: this.userId } })
      .then(response => {
        this.setState({ user: response.data.data[0] });
      })
      .catch(error => {
        console.log(error);
      });
  }

  render() {
    return (
      <div className='container-fluid'>
        <div className='row'>
          <div className='col-12 mt-4'>
            <div className='health-card-details pt-3 pb-5'>
              <div className='float-left pl-2'>
                <img
                  className='health-profile-details-picture'
                  src='../../images/team-users/3.jpg'
                  alt='Profile'
                />
              </div>
              <div className='float-left card-content'>
                <h5 className=''>{this.state.user.name}</h5>
                <p className='card-text'>{this.state.user.age} Yrs</p>
              </div>
              <div className='float-right health-links mb-3'>
                <a href='#/' title='Weekly' className='mr-4'>
                  Weekly
                </a>
                <a href='#/' title='Monthly' className='mr-4'>
                  Monthly{" "}
                </a>
                <a href='#/' title='Yearly' className='mr-5'>
                  Yearly
                </a>
              </div>
              <div className='clearfix'></div>
              <div className='col-12'>
                <div className='row'>
                  <div className='col-md-6 bg-white mb-3'>
                    <div className='border p-3 rounded'>
                      <div className='health-icon-font mb-3'>
                        <img src='../../images/heart.svg' alt='bp' />
                        <span className='health-icon-font pl-1'>
                          Blood Pressure
                        </span>
                      </div>
                      <BpChart user={this.state.user}></BpChart>
                    </div>
                  </div>
                  <div className='col-md-6 bg-white mb-3'>
                    <div className='border p-3 rounded'>
                      <div className='mb-3'>
                        <img src='../../images/weight.svg' alt='weight' />
                        <span className='health-icon-font pl-1'>Weight</span>
                      </div>
                      <WeightChart user={this.state.user}></WeightChart>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default UserDetails;
