import React from "react";
import { Link } from "react-router-dom";

function HealthCard(props) {
  return (
    <Link to={"/user-details/" + props.user.userId}>
      <div className='health-card'>
        <div className='float-left'>
          <img
            className='health-profile-picture'
            src='../../images/team-users/3.jpg'
            alt='Card Profile'
          />
        </div>
        <div className='float-left card-content dark-grey'>
          <h5 className=''>{props.user.name}</h5>
          <p className='card-text'>{props.user.age}Yrs</p>
        </div>
        <div className='clearfix'></div>
        <div className='health-card-body'>
          <div className='yellow health-icon health-icon-img-w'>
            <img src='../../images/heart.svg' alt='heart' />
          </div>
          <div className='green health-icon health-icon-img-w'>
            <img src='../../images/weight.svg' alt='weight' />
          </div>
        </div>
      </div>
    </Link>
  );
}

export default HealthCard;
