import React from "react";
import { Bar } from "react-chartjs-2";
import moment from "moment";

function WeightChart(props) {
  //format data
  let weightDates = [];
  let weightValues = [];
  let labelUnit = "";
  if (props.user.weight && props.user.weight.length) {
    labelUnit = props.user.weight[0].weightUnit;
    props.user.weight.forEach(function(weight) {
      const createdDate = moment(weight.createdDate).format("DD/MM");
      weightDates.push(createdDate);
      weightValues.push(weight.weightValue);
    });
  }

  const chartData = {
    labels: weightDates,
    datasets: [
      {
        label: `In ${labelUnit}`,
        backgroundColor: "green",
        borderColor: "blue",
        borderWidth: 1,
        data: weightValues
      }
    ]
  };

  return (
    <React.Fragment>
      <div style={{ height: "300px", width: "100%" }}>
        <Bar
          data={chartData}
          options={{
            maintainAspectRatio: false,
            title: {
              display: true,
              text: "Weekly Weight Chart",
              fontSize: 20
            },
            legend: {
              display: true,
              position: "bottom"
            },
            scales: {
              xAxes: [
                {
                  stacked: false
                }
              ],
              yAxes: [
                {
                  stacked: false,
                  ticks: {
                    beginAtZero: true,
                    min: 0,
                    max: 230
                  }
                }
              ]
            }
          }}
        />
      </div>
      <table className='table'>
        <tbody>
          {props.user.weight &&
            props.user.weight.map(weight => (
              <tr key={weight.weightId}>
                <td>
                  <i className='time-stamp'>
                    {moment(weight.createdDate).format("DD/MM/YYYY")}
                  </i>
                </td>
                <td>
                  {weight.weightValue}&nbsp;{weight.weightUnit}
                </td>
              </tr>
            ))}
        </tbody>
      </table>
    </React.Fragment>
  );
}

export default WeightChart;
