import React from "react";
import axios from "axios";
import HealthCard from "./../HealthCard/HealthCard";

class Dashboard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      users: []
    };
  }

  componentDidMount() {
    const url = "http://54.251.159.245";
    axios
      .get(`${url}/api/get-users`)
      .then(response => {
        console.log(response);
        const usersData = response.data.data;
        this.setState({ users: usersData });
      })
      .catch(error => {
        console.log(error);
      });
  }

  render() {
    let healthCards = [];
    this.state.users.forEach((user, i) => {
      healthCards.push(
        <div className='col-md-3 my-3' key={user.userId}>
          <HealthCard user={user}></HealthCard>
        </div>
      );
    });
    return (
      <div className='container-fluid'>
        <div className='row'>{healthCards}</div>
      </div>
    );
  }
}

export default Dashboard;
