/* eslint-disable jsx-a11y/anchor-is-valid */
import React from "react";
import userIcon from "../../images/user.png";
import { withRouter } from "react-router-dom";
import { createBrowserHistory } from "history";

function Header(props) {
  const history = createBrowserHistory();
  const adminLogout = () => {
    localStorage.clear();
    history.push("/");
  };
  return (
    history.location.pathname !== "/" && (
      <header className='mdl-layout__header'>
        <div className='mdl-layout__header-row mdl-color--white'>
          <span className='mdl-layout-title purple-lite welnessapp-logo ml-2'>
            WELLNESS APP
          </span>
          <div className='mdl-layout-spacer'></div>
          <nav className='mdl-navigation mdl-layout--large-screen-only'>
            <a href='#' className='mx-logout ml-2 mdl-color--white'>
              <div className='float-right mx-profile-pic '>
                <img
                  src={userIcon}
                  className='rounded-circle'
                  id='mx-logout'
                  alt='USERPIC'
                />
              </div>
              <div className='float-right pr-3 '>
                <h5 className='text-right mx-header-username'>
                  {localStorage.getItem("Admin")}
                </h5>
              </div>
            </a>
            <ul className='mdl-menu mdl-menu--bottom-right mdl-js-menu mdl-js-ripple-effect py-0'>
              <li className='mdl-menu__item'>
                <i className='material-icons mr-2'>exit_to_app</i>{" "}
                <a href='index.html'>Logout</a>
              </li>
            </ul>
          </nav>
        </div>
        <div className='mx-layout-tm-menu'>
          <div className='mx-layout-left-drawer-tm mx-sidebar-menu '>
            <ul className='mx-sidenavmenu-items'>
              <li>
                <a href='/' className='mx-left-drawer-menu active'>
                  <i className='material-icons '>home</i>
                  <span className='menu-label'>Home</span>
                </a>
              </li>
              <li>
                <a
                  href='#/'
                  className='mx-left-drawer-menu active'
                  onClick={adminLogout}
                >
                  <i className='material-icons '>logout</i>
                  <span className='menu-label'>Logout</span>
                </a>
              </li>
            </ul>
          </div>
        </div>
      </header>
    )
  );
}

export default withRouter(Header);
