import React from "react";
import { Bar } from "react-chartjs-2";
import moment from "moment";

function BpChart(props) {
  //format data
  let bpDates = [];
  let systolicData = [];
  let diastolicData = [];
  let pulseData = [];
  if (props.user.bp && props.user.bp.length) {
    props.user.bp.forEach(function(bp) {
      const createdDate = moment(bp.createdDate).format("DD/MM");
      bpDates.push(createdDate);
      systolicData.push(bp.systolic_high);
      diastolicData.push(bp.diastolic_low);
      pulseData.push(bp.pulse);
    });
  }

  const chartData = {
    labels: bpDates,
    datasets: [
      {
        label: "Pulse",
        type: "line",
        data: pulseData,
        fill: false,
        borderColor: "#EC932F",
        backgroundColor: "#EC932F",
        pointBorderColor: "#EC932F",
        pointBackgroundColor: "#EC932F",
        pointHoverBackgroundColor: "#EC932F",
        pointHoverBorderColor: "#EC932F"
      },
      {
        label: "High (Systolic)",
        backgroundColor: "pink",
        borderColor: "red",
        borderWidth: 1,
        data: systolicData
      },
      {
        label: "Low (Diastolic)",
        backgroundColor: "lightblue",
        borderColor: "blue",
        borderWidth: 1,
        data: diastolicData
      }
    ]
  };
  return (
    <React.Fragment>
      <div style={{ height: "300px", width: "100%" }}>
        <Bar
          data={chartData}
          options={{
            maintainAspectRatio: false,
            title: {
              display: true,
              text: "Weekly Blood Pressure Chart",
              fontSize: 20
            },
            legend: {
              display: true,
              position: "bottom"
            },
            scales: {
              xAxes: [
                {
                  stacked: false
                }
              ],
              yAxes: [
                {
                  stacked: false,
                  ticks: {
                    beginAtZero: true,
                    min: 0,
                    max: 230
                  }
                }
              ]
            }
          }}
        />
      </div>
      <table className='table'>
        <tbody>
          {props.user.bp &&
            props.user.bp.map(bp => (
              <tr key={bp.bpId}>
                <td>
                  <i className='time-stamp'>
                    {moment(bp.createdDate).format("DD/MM/YYYY")}
                  </i>
                </td>
                <td>
                  {bp.systolic_high} / {bp.diastolic_low}
                </td>
                <td>Pulse: {bp.pulse}</td>
              </tr>
            ))}
        </tbody>
      </table>
    </React.Fragment>
  );
}

export default BpChart;
