import React, { Component } from "react";
import "./Login.css";
import axios from "axios";

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      userName: "",
      password: "",
      errorResponse: ""
    };
    this.handleUserNameChange = this.handleUserNameChange.bind(this);
    this.handlePasswordChange = this.handlePasswordChange.bind(this);
    this.handleFormSubmit = this.handleFormSubmit.bind(this);
    this.url = "http://54.251.159.245";
  }
  handleUserNameChange(e) {
    this.setState({
      userName: e.target.value
    });
  }
  handlePasswordChange(e) {
    this.setState({
      password: e.target.value
    });
  }
  handleFormSubmit(e) {
    e.preventDefault();
    axios
      .post(`${this.url}/api/admin-login`, {
        userName: this.state.userName,
        password: this.state.password
      })
      .then(response => {
        if (response && response.data && response.data.success === true) {
          localStorage.setItem("token", response.data.data.token);
          localStorage.setItem("isAuthenticated", response.data.success);
          localStorage.setItem("Admin", this.state.userName);
          this.props.history.push("/dashboard");
          this.setState({ errorResponse: "" });
        } else {
          console.log("failure");
          this.setState({ errorResponse: response.data.message });
        }
      })
      .catch(error => {
        this.setState({
          errorResponse: error
        });
      });
  }
  render() {
    return (
      <div className='login'>
        <h5>Wellness App</h5>
        <form>
          <div className='login-card'>
            <div className='login-input-box'>
              <label htmlFor='user-name'>User Name</label>
              <input
                type='text'
                id='user-name'
                autoComplete='false'
                onChange={this.handleUserNameChange}
              />
            </div>
            <div className='login-input-box'>
              <label htmlFor='password'>Password</label>
              <input
                type='password'
                id='password'
                onChange={this.handlePasswordChange}
              />
            </div>
            <button
              type='submit'
              disabled={
                this.state.userName === "" || this.state.password === ""
              }
              onClick={this.handleFormSubmit}
            >
              Login
            </button>
            <p className={this.state.errorResponse === "" ? "hidden" : "show"}>
              {this.state.errorResponse}
            </p>
          </div>
        </form>
      </div>
    );
  }
}
export default Login;
